
#body {
    background-color: #f00;
    flex: 1;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    justify-self: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.row {
    display: flexbox;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -moz-box;
    display: -webkit-flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
.column {
    display: flexbox;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -moz-box;
    display: -webkit-flex;
    flex-basis: 100%;
    flex: 1;
}

#page {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

h1#main {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-align: center;
}

h3#main {
	padding: 5px 20px;
	margin: 0;
}

.clickable {
    cursor: pointer;
}

.notClickable {
    cursor: default;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.alignSelf {
    align-self: center;
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: center;
    justify-self: center
}
